/**
 * æ ¼å¼åŒ–æŒç»­æ—¶é—´ï¼ˆç§’ï¼‰ä¸ºå¯è¯»æ ¼å¼
 * @param seconds ç§’æ•°
 * @returns æ ¼å¼åŒ–åçš„æŒç»­æ—¶é—´å­—ç¬¦ä¸²
 */
export const formatDuration = (seconds: number | undefined): string => {
  // æ£€æŸ¥æ˜¯å¦ä¸ºæœ‰æ•ˆæ•°å­—
  if (!seconds || seconds <= 0 || isNaN(seconds) || !isFinite(seconds)) {
    return '-';
  }
  
  if (seconds < 60) {
    return `${seconds}ç§’`;
  } else if (seconds < 3600) {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes}åˆ†${remainingSeconds}ç§’`;
  } else {
    const hours = Math.floor(seconds / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);
    const remainingSeconds = seconds % 60;
    return `${hours}æ—¶${minutes}åˆ†${remainingSeconds}ç§’`;
  }
};

/**
 * è·å–ç›¸å¯¹æ—¶é—´æè¿°ï¼ˆå¦‚ï¼šåˆšåˆšã€5åˆ†é’Ÿå‰ã€1å°æ—¶å‰ç­‰ï¼‰
 * @param date æ—¥æœŸå¯¹è±¡æˆ–æ—¥æœŸå­—ç¬¦ä¸²
 * @returns ç›¸å¯¹æ—¶é—´æè¿°
 */
export const getRelativeTime = (date: Date | string | undefined): string => {
  if (!date) return '-';
  
  try {
    const dateObj = typeof date === 'string' ? new Date(date) : date;
    
    if (isNaN(dateObj.getTime())) {
      return '-';
    }
    
    const now = new Date();
    const diffMs = now.getTime() - dateObj.getTime();
    const diffSeconds = Math.floor(diffMs / 1000);
    const diffMinutes = Math.floor(diffSeconds / 60);
    const diffHours = Math.floor(diffMinutes / 60);
    const diffDays = Math.floor(diffHours / 24);
    
    if (diffSeconds < 60) {
      return 'åˆšåˆš';
    } else if (diffMinutes < 60) {
      return `${diffMinutes}åˆ†é’Ÿå‰`;
    } else if (diffHours < 24) {
      return `${diffHours}å°æ—¶å‰`;
    } else if (diffDays < 7) {
      return `${diffDays}å¤©å‰`;
    } else {
      return formatDate(dateObj);
    }
  } catch (error) {
    console.error('ç›¸å¯¹æ—¶é—´è®¡ç®—é”™è¯¯:', error);
    return '-';
  }
};
